<template>
  <div>
    <!---->
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" style="width: 200px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="searchInfo"></el-button>
    <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading"
              element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="" type="index" width="50"/>
      <el-table-column prop="name" label="" width="100"/>
      <el-table-column prop="juniorNumber" label=""/>
      <el-table-column prop="juniorGrade" label="" width="100"/>
      <el-table-column prop="mainAcademy" label=""/>
      <el-table-column prop="sex" label="" width="50"/>
      <el-table-column prop="majorName" label=""/>
      <el-table-column prop="department" label=""/>
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" inline class="demo-table-expand">
            <el-form-item label=":">
              <span>{{ props.row.name }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.idNumber }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span v-if="props.row.sex==''" style="color: red">
                {{ props.row.sex }}
              </span>
              <span v-else style="color: blue">
                 {{ props.row.sex }}
              </span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.birthDate }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.juniorNumber }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.juniorClass }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.juniorGrade }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.mainAcademy }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.majorNumber }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.majorName }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.examRegistrationNumber }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.department }}</span>
            </el-form-item>
            <el-form-item label=":">
              <span>{{ props.row.gradeNumber }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
    </el-table>
    <!--,-->
    <div class="footer">
      <el-pagination background
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-sizes="counts"
                     :page-size="count"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="totalCount">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        //
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        //
        counts: [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
        department: [
          {
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }],
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'department', label: ''},
          {value: 'mainAcademy', label: ''},
          {value: 'major', label: ''},
        ],
        loading: true
      }
    },
    methods: {
      //
      handleSizeChange (val) {
        switch (val) {
          case 100:
          case 200:
          case 300:
          case 400:
          case 500:
            this.count = val
            break
        }
        this.handleCurrentChange(val)
      },
      //
      handleCurrentChange (val) {
        // 
        this.currentPage = val
        this.searchInfo()
      },
      //
      searchInfo () {
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          currentPage: this.currentPage,
          count: this.count
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.juniorNumber = key
        } else if (item === 'department') {
          condition.department = key
        } else if (item === 'mainAcademy') {
          condition.mainAcademy = key
        } else {
          condition.majorName = key
        }
        //
        let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
        this.axios.get(url, {
          params: condition
        }).then(response => {
          //
          let pageInfo = response.data.pageInfo
          this.currentPage = pageInfo.currentPage
          this.totalPage = pageInfo.totalPage
          this.count = pageInfo.count
          this.totalCount = pageInfo.totalCount
          //
          this.tableData = response.data.data
          this.loading = false
        }).catch(error => {
          this.loading = false
        }).finally(() => {
          this.loading = false
        })
      },
      //
      reset () {
        this.itemSelect = 'name'
        this.searchKeys = ''
        this.loading = true
        let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
        this.axios.get(url, {
          params: {
            currentPage: 1,
            count: this.count
          }
        }).then(response => {
          //
          let pageInfo = response.data.pageInfo
          this.currentPage = pageInfo.currentPage
          this.totalPage = pageInfo.totalPage
          this.count = pageInfo.count
          this.totalCount = pageInfo.totalCount
          //
          this.tableData = response.data.data
          this.loading = false
        }).catch(error => {
          this.loading = false
        })
        this.loading = false
      }
    },
    //
    mounted () {
      let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
      this.axios.get(url, {
        params: {
          currentPage: 1,
          count: this.count
        }
      }).then(response => {
        //
        let pageInfo = response.data.pageInfo
        this.currentPage = pageInfo.currentPage
        this.totalPage = pageInfo.totalPage
        this.count = pageInfo.count
        this.totalCount = pageInfo.totalCount
        //
        this.tableData = response.data.data
        this.loading = false
      }).catch(error => {
        this.loading = false
      }).finally(() => {
        console.log('finally')
        this.loading = false
      })
    }
  }
</script>

<style scoped>
  .footer {
    text-align: center;
  }

  .demo-table-expand {
    font-size: 0;
  }

  .demo-table-expand label {
    width: 90px;
    color: #99a9bf;
  }

  .demo-table-expand .el-form-item {
    margin-right: 0;
    margin-bottom: 0;
    width: 50%;
  }
</style>



