<template>
  <el-form :model="form" :rules="rules" ref="login_form" label-width="100px"
           class="login-form">
    <p></p>
    <el-form-item label=":" prop="oldPass">
      <el-input v-model="form.oldPass" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="newPass">
      <el-input v-model="form.newPass" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="againPass">
      <el-input v-model="form.againPass" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="alter"></el-button>
      <el-button @click="reset"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'AlterPassWord',
    data () {
      return {
        form: {
          oldPass: '',
          againPass: '',
          newPass: '',
        },
        rules: {
          oldPass: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          newPass: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          againPass: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      alter () {
        let vm = this
        let oldPass = this.form.oldPass
        let newPass = this.form.newPass
        let againPass = this.form.againPass
        this.$refs.login_form.validate((valid) => {
          if (valid) {
            if (newPass !== againPass) {
              this.$message.error(',')
            } else {
              //
              this.axios(vm.url_request.ip_port_dev + vm.url_request.root_request + '/alter_password', {
                method: 'post',
                data: {
                  password: oldPass,
                  newPassWord: newPass
                }
              }).then(response => {
                let result = response.data
                this.$message({
                  title: '',
                  message: result,
                  type: 'info',
                  center: true
                })
              }).catch(error => {
                this.$message({
                  title: '',
                  message: ',!',
                  type: 'error',
                  center: true
                })
              })
            }
          }
        })
      },
      reset () {
        this.$refs.login_form.resetFields()
      }
    }
  }
</script>

<style scoped>
  .login-form {
    width: 40%;
    text-align: center;
    margin: 100px auto;
  }

  .el-input {
    width: 80%;
  }
</style>



