<template>
  <div>
    <div class="header">
      <el-button type="success"></el-button>
      <el-select v-model="itemSelect" style="width: 120px">
        <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                   :value="item.value">
        </el-option>
      </el-select>
      <el-input placeholder="" style="width: 200px"
                v-model="searchKeys" clearable/>
      <el-button icon="el-icon-search" plain></el-button>
      <el-button icon="el-icon-refresh" plain></el-button>
    </div>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="" type="index" width="50"/>
      <el-table-column align="center" prop="name" label=""/>
      <el-table-column align="center" prop="username" label=""/>
      <el-table-column align="center" prop="createdDate" label=""/>
      <el-table-column align="center" label="" width="80">
        <template slot-scope="props">
          <span v-if="props.row.state==='1'" style="color: green">
            
          </span>
          <span v-else style="color: red">
          
        </span>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.state==='0'">
         <el-button size="mini" type="success" @click="column_delete(scope.$index)">
          </el-button>
          </span>
          <span v-else>
            <el-button size="mini" type="danger" @click="column_delete(scope.$index)">
           </el-button>
          </span>
          <el-button size="mini" type="primary" @click="showDialog(1,scope.$index)">
          </el-button>
          <el-button size="mini" type="danger" @click="column_delete(scope.$index)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--,-->
    <div class="footer">
      <el-pagination background
                     @size-change="handleSizeChange" @current-change="handleCurrentChange"
                     :current-page="currentPage" :page-sizes="counts"
                     :page-size="count"
                     layout="total, sizes, prev, pager, next, jumper" :total="totalCount">
      </el-pagination>
    </div>
    <!---->
    <el-dialog :title="title" :visible.sync="dialogFormVisible"
               :center="true" :close-on-click-modal="false">
      <el-form :model="form" :rules="rules" :inline="true" ref="dialog_form">
        <el-form-item label=":">
          <el-input/>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="dialogFormVisible=false"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: 'UserManage',
    data () {
      return {
        loading: true,
        title: '',
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'username', label: ''},
          {value: 'state', label: ''},
        ],
        dialogFormVisible: false,
        form: {},
        rules: {},
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts:
          [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
      }
    },
    methods: {
      //
      handleSizeChange (val) {
        switch (val) {
          case 100:
          case 200:
          case 300:
          case 400:
          case 500:
            this.count = val
            break
        }
        this.handleCurrentChange(val)
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.searchInfo()
      }
    },
    mounted () {
      let url = this.url_request.ip_port_dev + '/user_manage_query'
      this.axios.get(url, {
        params: {
          currentPage: 1,
          count: this.count
        }
      }).then(response => {
        this.tableData = response.data
        this.loading = false
      }).catch(error => {
        this.loading = false
      })
    }
  }
</script>

<style scoped>
  .header {
    text-align: left;
  }
</style>



