<template>
  <el-menu :default-openeds="['1']">
    <el-submenu index="1" v-if="hasTeacherRole||hasAdminRole">
      <template slot="title"><i class="el-icon-message"></i></template>
      <el-menu-item index="1-1">
        <router-link to="/main/studentInfoCheck"></router-link>
      </el-menu-item>
      <el-menu-item index="1-2">
        <router-link to="/main/studentInfoMaintain"></router-link>
      </el-menu-item>
      <el-menu-item index="1-3">
        <router-link to="/main/studentExport"></router-link>
      </el-menu-item>
      <el-menu-item index="1-4">
        <router-link to="/main/studentInfoImport"></router-link>
      </el-menu-item>
    </el-submenu>
    <el-submenu index="2" v-if="hasTeacherRole||hasAdminRole">
      <template slot="title"><i class="el-icon-message"></i></template>
      <el-menu-item index="2-1">1</el-menu-item>
      <el-menu-item index="2-2">2</el-menu-item>
    </el-submenu>
    <el-submenu index="3" v-if="hasTeacherRole||hasAdminRole">
      <template slot="title"><i class="el-icon-message"></i></template>
      <el-menu-item index="3-1">1</el-menu-item>
      <el-menu-item index="3-2">2</el-menu-item>
    </el-submenu>
    <el-submenu index="4" v-if="hasStudentRole||hasAdminRole">
      <template slot="title"><i class="el-icon-message"></i></template>
      <el-menu-item index="4-1">
        <router-link to="/main/onlineExamination"></router-link>
      </el-menu-item>
      <el-menu-item index="4-2"></el-menu-item>
    </el-submenu>
    <el-submenu index="5" v-if="hasStudentRole||hasAdminRole">
      <template slot="title"><i class="el-icon-message"></i></template>
      <el-menu-item index="5-1">
        <router-link to="/main/onlineExamination"></router-link>
      </el-menu-item>
      <el-menu-item index="5-2"></el-menu-item>
    </el-submenu>
    <el-submenu index="6" v-if="hasPublicRole||hasAdminRole">
      <template slot="title"><i class="el-icon-message"></i></template>
      <el-menu-item index="6-1">
        <router-link to="/main/alterPassWord"></router-link>
      </el-menu-item>
      <el-menu-item index="6-2">2</el-menu-item>
    </el-submenu>
    <el-submenu index="7" v-if="hasAdminRole">
      <template slot="title"><i class="el-icon-message"></i></template>
      <el-menu-item index="7-1">
        <router-link to="/main/userManage"></router-link>
      </el-menu-item>
    </el-submenu>
    <el-submenu index="8" v-if="hasPublicRole">
      <template slot="title"><i class="el-icon-message"></i></template>
      <el-menu-item index="8-1"></el-menu-item>
      <el-menu-item index="8-2"></el-menu-item>
    </el-submenu>
  </el-menu>

</template>
<script>
  export default {
    name: 'submenu',
    data () {
      return {
        //
        hasTeacherRole: false,
        //
        hasStudentRole: false,
        //
        hasAdminRole: false,
        //, 
        hasPublicRole: true
      }
    },
    mounted () {
      //sessionStorage
      let role = sessionStorage.getItem('role')
      if (role === '0') {
        this.hasStudentRole = true
      } else if (role === '1') {
        this.hasTeacherRole = true
      } else if (role === '2') {
        this.hasAdminRole = true
      }
    }
  }
</script>

<style scoped>
  a {
    /**/
    text-decoration: none;
  }

</style>



