<template>
  <div class="header">
    <span class="left">
      
    </span>
    :{{name}}
    :
    <span v-if="this.role==='0'">
      
    </span>
    <span v-if="this.role==='1'">
      
    </span>
    <span v-if="this.role==='2'">
      
    </span>
    <el-button type="danger" @click="logout"></el-button>
  </div>
</template>
<script>
  export default {
    name: 'Header',
    data () {
      return {
        name: '',
        role: ''
      }
    },
    methods: {
      logout () {
        this.$router.push('/login')
      }
    },
    mounted () {
      this.name = sessionStorage.getItem('name')
      this.role = sessionStorage.getItem('role')
    }
  }
</script>

<style scoped>
  .header {
    text-align: center;
  }

  .left {
    margin-right: 500px;
  }
</style>



