<template>
  <div>
    <el-form :model="form" :rules="rules" ref="login_form" label-width="100px"
             class="login-form">
      <p></p>
      <el-form-item label=":" prop="username">
        <el-input v-model="form.username" clearable
                  prefix-icon="my-icon-user" placeholder=""/>
      </el-form-item>
      <el-form-item label=":" prop="password">
        <el-input v-model="form.password" show-password
                  prefix-icon="my-icon-password"
                  @keyup.enter.native="login" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="login"></el-button>
        <el-button @click="reset"></el-button>
        <el-popover placement="top" width="160" v-model="visible">
          <p></p>
          <div style="text-align: right; margin: 0">
            <el-button size="mini" type="text" @click="ownRetrieve"></el-button>
            <el-button type="primary" size="mini" @click="personRetrieve"></el-button>
          </div>
          <el-button slot="reference"></el-button>
        </el-popover>
      </el-form-item>
      <el-form-item>
        <router-link class="register" to="/register"></router-link>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  export default {
    name: 'Login',
    data () {
      return {
        form: {
          username: '',
          password: ''
        },
        visible: false,
        rules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      login () {
        const vm = this
        const username = this.form.username
        const password = this.form.password
        this.$refs.login_form.validate((valid) => {
          if (valid) {
            //
            this.axios(vm.url_request.ip_port_dev + '/login', {
              method: 'post',
              data: {
                username: username,
                password: password
              }
            }).then(response => {
              let result = response.data.message
              let role = response.data.role
              let name = response.data.name
              if (result === vm.login_show.success) {
                vm.$router.replace('/main')
                //,,
                sessionStorage.setItem('username', username)
                sessionStorage.setItem('name', name)
                sessionStorage.setItem('role', role)
              } else {
                this.$message({
                  title: '',
                  message: result,
                  type: 'error',
                  center: true
                })
              }
            }).catch(error => {
              this.$message({
                title: '',
                message: ',!',
                type: 'error',
                center: true
              })
            })
          } else {
            return false
          }
        })
      },
      reset () {
        this.$refs.login_form.resetFields()
      },
      //
      ownRetrieve () {
        this.$router.replace('/forgetPassByOwn')
      },
      personRetrieve () {
        this.$router.replace('/forgetPassByPerson')
      }
    }
  }
</script>

<style scoped>
  .login-form {
    width: 50%;
    text-align: center;
    margin: 100px auto;
  }

  .el-input {
    width: 80%;
  }

  .register {
    text-decoration: none;
    font-size: large;
    color: coral;
  }
</style>




