import Vue from 'vue'
import Vuex from 'vuex'
//
Vue.use(Vuex)
//store
export default new Vuex.Store({
  state,//
  mutations,//state
  actions,//
  getters,//getter
})

//
const state = {
  count: 0,
  role: 1,
  permission: 'admin'
}
//state
const mutations = {
  //statevuex
  add (state) {
    state.count++
    console.log('add');
  },
  reduce (state) {
    state.count--
  }
}
//
const actions = {
  //actionsstate,mutations
  //commitstate
//{commit,state},2
  increment ({commit, state}) {
    commit('add')
  },
  decrement ({commit}) {
    commit('reduce')
  },
  incrementIfOdd ({commit, state}) {
    if (state.count % 2 === 1) {
      commit('add')
    }
  },
  incrementAsync ({commit}) {
    setTimeout(() => {
      commit('add')
    }, 1000)
  }
}
//getter
const getters = {
  //statevuexvuex
  evenOrOdd (state) {
    return state.count % 2 === 0 ? '' : ''
  }
}




