<template>
  <div>
    <el-button type="success" @click="showDialog(0)"></el-button>
    <el-select v-model="itemSelect" style="width: 120px">
      <el-option v-for="item in searchCondition" :key="item.value" :label="item.label"
                 :value="item.value">
      </el-option>
    </el-select>
    <el-input placeholder="" style="width: 200px"
              v-model="searchKeys" clearable/>
    <el-button icon="el-icon-search" plain @click="searchInfo"></el-button>
    <el-button icon="el-icon-refresh" plain @click="reset"></el-button>
    <!-- max-heightheight -->
    <el-table :data="tableData" stripe border max-height="500" height="450"
              v-loading="loading" element-loading-text=""
              element-loading-spinner="el-icon-loading"
              element-loading-background="rgba(0, 0, 0, 0.8">
      <el-table-column label="" type="index" width="50"/>
      <el-table-column prop="name" label="" width="80"/>
      <el-table-column prop="juniorNumber" label=""/>
      <el-table-column prop="juniorGrade" label="" width="80"/>
      <el-table-column prop="mainAcademy" label=""/>
      <el-table-column prop="sex" label="" width="50"/>
      <el-table-column prop="majorName" label=""/>
      <el-table-column prop="department" label="" width="200"/>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button size="mini" @click="showDialog(1,scope.$index)">
          </el-button>
          <el-button size="mini" type="danger" @click="column_delete(scope.$index)">
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--,-->
    <div class="footer">
      <el-pagination background
                     @size-change="handleSizeChange" @current-change="handleCurrentChange"
                     :current-page="currentPage" :page-sizes="counts"
                     :page-size="count"
                     layout="total, sizes, prev, pager, next, jumper" :total="totalCount">
      </el-pagination>
    </div>
    <!---->
    <el-dialog :title="title" :visible.sync="dialogFormVisible"
               :center="true" :close-on-click-modal="false" @open="open">
      <el-form :model="form" :rules="rules" :inline="true" ref="dialog_form">
        <el-form-item label=":" prop="name">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="idNumber">
          <el-input v-model="form.idNumber"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="birthDate">
          <el-date-picker v-model="form.birthDate" style="width: 200px;"
                          type="date" placeholder=""/>
        </el-form-item>
        <el-form-item label=":" prop="juniorNumber">
          <el-input v-model="form.juniorNumber"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="juniorClass">
          <el-input v-model.number="form.juniorClass"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="juniorGrade">
          <el-input v-model.number="form.juniorGrade"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="mainAcademy">
          <el-input v-model="form.mainAcademy"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="majorNumber">
          <el-input v-model="form.majorNumber"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="majorName">
          <el-input v-model="form.majorName"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="examRegistrationNumber">
          <el-input v-model="form.examRegistrationNumber"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="department">
          <el-select v-model="form.department" style="width: 200px;" placeholder="">
            <el-option
              v-for="item in department"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label=":" prop="gradeNumber">
          <el-input v-model="form.gradeNumber"></el-input>
        </el-form-item>
        <el-form-item label=":" prop="sex">
          <el-radio v-model="form.sex" label=""></el-radio>
          <el-radio v-model="form.sex" label=""></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="ensure"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  export default {
    name: 'StudentInfoMaintain',
    data () {
      return {
        //
        tableData: [],
        //
        currentPage: 1,
        //
        totalPage: 1,
        counts: [100, 200, 300, 400, 500],
        //
        count: 100,
        //
        totalCount: 1,
        //
        itemSelect: 'name',
        searchKeys: '',
        title: '',
        dialogFormVisible: false,
        department: [
          {
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }, {
            value: '',
            label: ''
          }],
        searchCondition: [
          {value: 'name', label: ''},
          {value: 'number', label: ''},
          {value: 'department', label: ''},
          {value: 'mainAcademy', label: ''},
          {value: 'major', label: ''},
        ],
        form: {
          id: -1,
          name: '',
          idNumber: '',
          birthDate: '',
          juniorNumber: '',
          juniorClass: '',
          juniorGrade: '',
          mainAcademy: '',
          majorNumber: '',
          majorName: '',
          examRegistrationNumber: '',
          department: '',
          sex: '',
          gradeNumber: ''
        },
        loading: true,
        //
        rules: {
          name: [
            {required: true, message: ''},
            {min: 2, max: 6, message: '2-6'}
          ],
          idNumber: [
            {required: true, message: ''},
            {min: 10, max: 20, message: '10-20'}
          ],
          birthDate: [{required: true, message: ''}],
          juniorNumber: [
            {required: true, message: ''},
            {min: 8, max: 20, message: '8-20'}
          ],
          juniorClass: [
            {required: true, message: ''},
            {
              type: 'number', message: '',
              min: 1, max: 999999999, message: '1-999999999'
            }
          ],
          juniorGrade: [
            {required: true, message: ''},
            {
              type: 'number', message: '',
              min: 1, max: 999999999, message: '1-99999999'
            }
          ],
          mainAcademy: [
            {required: true, message: ''},
            {min: 6, max: 20, message: '6-20'}
          ],
          majorNumber: [
            {required: true, message: ''},
            {min: 6, max: 20, message: '6-20'}
          ],
          majorName: [
            {required: true, message: ''},
            {min: 4, max: 20, message: '4-20'}
          ],
          examRegistrationNumber: [
            {required: true, message: ''},
            {min: 6, max: 20, message: '6-20'}
          ],
          sex: [
            {required: true}
          ],
          department: [
            {required: true, message: ''},
            {min: 6, max: 20, message: '6-20'}
          ],
          gradeNumber: [
            {required: true, message: ''},
            {min: 6, max: 20, message: '6-20'}
          ]
        }
      }
    },
    methods: {
      /*
      *
      * @param type 
      * @param data 
      * */
      open (type, data) {
        //
        this.$nextTick(() => {
          this.$refs.dialog_form.resetFields()
          switch (type) {//12
            case 0:
              this.title = ''
              break
            case 1:
              this.title = ''
              this.form = data
              break
            default:
              break
          }
        })
      },
      //
      column_delete (index, row) {
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: ''
        }).then(() => {
          //
          let id = this.tableData[index].id
          let url = this.url_request.ip_port_dev +
            this.url_request.root_request + '/student_manage/delete'
          this.addOrUpdateOrDelete(url, {id: id})
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          })
        })
      },
      /**
       * 
       * @param what 01
       * @param index ,
       */
      showDialog (what, index) {
        this.dialogFormVisible = true
        if (what === 0) {
          this.open(0)
        } else {
          this.title = ''
          //
          let object = Object.assign({}, this.tableData[index])
          this.open(1, object)
        }
      },
      //
      ensure () {
        //
        this.$refs.dialog_form.validate((validate) => {
          if (validate) {
            //
            let id = this.form.id
            let name = this.form.name
            let idNumber = this.form.idNumber
            let birthDate = this.form.birthDate
            let juniorNumber = this.form.juniorNumber
            let juniorClass = this.form.juniorClass
            let juniorGrade = this.form.juniorGrade
            let mainAcademy = this.form.mainAcademy
            let majorNumber = this.form.majorNumber
            let majorName = this.form.majorName
            let examRegistrationNumber = this.form.examRegistrationNumber
            let department = this.form.department
            let sex = this.form.sex
            let gradeNumber = this.form.gradeNumber
            let studentInfo = {
              id, name, idNumber, birthDate, juniorNumber,
              juniorClass, juniorGrade, mainAcademy,
              majorNumber, majorName, examRegistrationNumber, department,
              sex, gradeNumber
            }
            let url = this.url_request.ip_port_dev + this.url_request.root_request
            //
            if (id < 0) {
              //
              url += '/student_manage/add'
              this.addOrUpdateOrDelete(url, studentInfo)
            } else {
              //
              url += '/student_manage/update'
              this.addOrUpdateOrDelete(url, studentInfo)
            }
            this.dialogFormVisible = false
          } else {
            return false
          }
        })
      },
      //
      searchInfo () {
        let item = this.itemSelect
        let key = this.searchKeys.trim()
        let condition = {
          currentPage: this.currentPage,
          count: this.count
        }
        if (item === 'name') {
          condition.name = key
        } else if (item === 'number') {
          condition.juniorNumber = key
        } else if (item === 'department') {
          condition.department = key
        } else if (item === 'mainAcademy') {
          condition.mainAcademy = key
        } else {
          condition.majorName = key
        }
        //
        let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
        this.axios.get(url, {
          params: condition
        }).then(response => {
          //
          let pageInfo = response.data.pageInfo
          this.currentPage = pageInfo.currentPage
          this.totalPage = pageInfo.totalPage
          this.count = pageInfo.count
          this.totalCount = pageInfo.totalCount
          //
          this.tableData = response.data.data
          this.loading = false
        }).catch(error => {
          this.loading = false
        })
      },
      //
      reset () {
        this.itemSelect = 'name'
        this.searchKeys = ''
        this.loading = true
        let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
        this.axios.get(url, {
          params: {
            currentPage: 1,
            count: this.count
          }
        }).then(response => {
          //
          let pageInfo = response.data.pageInfo
          this.currentPage = pageInfo.currentPage
          this.totalPage = pageInfo.totalPage
          this.count = pageInfo.count
          this.totalCount = pageInfo.totalCount
          //
          this.tableData = response.data.data
          this.loading = false
        }).catch(error => {
          this.loading = false
        })
      },
      /**
       * 
       * @param url 
       * @param student 
       */
      addOrUpdateOrDelete (url, student) {
        //
        this.axios(url, {
          method: 'post',
          data: student
        }).then(response => {
          let result = response.data
          this.reset()
          this.$message({
            title: '',
            message: result,
            type: 'success',
            center: true
          })
        }).catch(error => {
          this.$message({
            title: '',
            message: ',!',
            type: 'error',
            center: true
          })
        })
      },
      //
      handleSizeChange (val) {
        switch (val) {
          case 100:
          case 200:
          case 300:
          case 400:
          case 500:
            this.count = val
            break
        }
        this.handleCurrentChange(val)
      },
      //
      handleCurrentChange (val) {
        //
        this.currentPage = val
        this.searchInfo()
      }
    }
    ,
    mounted () {
      let url = this.url_request.ip_port_dev + this.url_request.root_request + '/student_check'
      this.axios.get(url, {
        params: {
          currentPage: 1,
          count: this.count
        }
      }).then(response => {
        //
        let pageInfo = response.data.pageInfo
        this.currentPage = pageInfo.currentPage
        this.totalPage = pageInfo.totalPage
        this.count = pageInfo.count
        this.totalCount = pageInfo.totalCount
        //
        this.tableData = response.data.data
        this.loading = false
      }).catch(error => {
        this.loading = false
      })
    }
  }
</script>

<style scoped>
  .el-input {
    width: 200px;
  }

  .el-table {
    width: 100%;
    margin-top: 10px
  }

  .footer {
    text-align: center;
  }
</style>



