<template>
  <el-form :model="form" :rules="rules" ref="login_form" label-width="100px"
           class="login-form">
    <p></p>
    <el-form-item label=":" prop="username">
      <el-input v-model="form.username" clearable
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="password">
      <el-input v-model="form.password" show-password
                @keyup.enter.native="login" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="again">
      <el-input v-model="form.again" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="name">
      <el-input v-model="form.name" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item label=":" prop="number">
      <el-input v-model="form.number" show-password
                placeholder=""></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="register"></el-button>
      <el-button @click="reset"></el-button>
    </el-form-item>
    <el-form-item>
      <router-link class="back" to="/login"></router-link>
    </el-form-item>
  </el-form>
</template>
<script>
  export default {
    name: 'Register',
    data () {
      return {
        form: {
          username: '',
          password: '',
          again: '',
          name: '',
          number: ''
        },
        rules: {
          username: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          again: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-10', trigger: 'blur'}
          ],
          number: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ]
        }
      }
    },
    methods: {
      register () {
        const vm = this
        const username = this.form.username
        const password = this.form.password
        const role = this.form.role
        this.$refs.login_form.validate((valid) => {
          if (valid) {
            //
            this.axios(vm.url_request.ip_port_dev + '/login', {
              method: 'post',
              data: {
                username: username,
                password: password
              }
            }).then(response => {
              let result = response.data.message
              console.log(response.data)
              if (result === vm.login_show.success) {
                vm.$router.replace('/main')
                //,
              } else {
                this.$message({
                  title: '',
                  message: result,
                  type: 'error',
                  center: true
                })
              }
            }).catch(error => {
              this.$message({
                title: '',
                message: ',!',
                type: 'error',
                center: true
              })
            })
          } else {
            return false
          }
        })
      },
      reset () {
        this.$refs.login_form.resetFields()
      }
    }
  }
</script>

<style scoped>
  .login-form {
    width: 50%;
    text-align: center;
    margin: 100px auto;
  }

  .el-input {
    width: 80%;
  }

  .back {
    text-decoration: none;
    font-size: large;
    color: coral;
  }

</style>



